.explore {
  &__item {
    margin-bottom: 48px;
    display: grid;
  }

  &__photo {
    margin-bottom: 24px;
    grid-template-columns: 1 / 3;
    width: 100%;

    @include onDesktop {
      margin-bottom: 32px;
    }

    &-container {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      column-gap: 24px;
    }
  }
  &__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
    text-align: center;
    color: #d12d35;
    margin-bottom: 4px;
  }

  &__subtitle {
    text-align: center;
  }
}
